import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import useAutoLogout from '../hooks/useAutoLogout';
import { installationService } from '../api/installationService';
import { Grid, Typography, Box, List, ListItem, ListItemIcon, ListItemText, Paper } from '@mui/material';
import Header from '../components/Header';
import { 
  Code as CodeIcon,
  Language as LanguageIcon,
  Build as BuildIcon
} from '@mui/icons-material';
import AppCard from '../components/AppCard';
import LogoutWarning from '../components/LogoutWarning';
import './HomePage.css';

const categories = [
  { id: 'all', name: 'All Software', icon: <BuildIcon /> },
  { id: 'development', name: 'Development', icon: <CodeIcon /> },
  { id: 'internet', name: 'Internet', icon: <LanguageIcon /> }
];

const softwareList = [
  {
    id: 'sublime-text',
    name: 'Sublime Text',
    developer: 'Sublime HQ',
    description: 'A sophisticated text editor for code, markup and prose',
    icon: 'https://www.sublimetext.com/images/icon.png',
    rating: 4.8,
    category: 'Development'
  },
  {
    id: 'visual-studio-code',
    name: 'Visual Studio Code',
    developer: 'Microsoft',
    description: 'Free and powerful source code editor',
    icon: 'https://code.visualstudio.com/assets/images/code-stable.png',
    rating: 4.9,
    category: 'Development'
  },
  {
    id: 'node',
    name: 'Node.js',
    developer: 'OpenJS Foundation',
    description: 'JavaScript runtime built on Chrome\'s V8 JavaScript engine',
    icon: 'https://nodejs.org/static/images/logo.svg',
    rating: 4.8,
    category: 'Development'
  },
  {
    id: 'postman',
    name: 'Postman',
    developer: 'Postman Inc.',
    description: 'API platform for building and using APIs',
    icon: 'https://cdn.worldvectorlogo.com/logos/postman.svg',
    rating: 4.7,
    category: 'Development'
  },
  {
    id: 'docker',
    name: 'Docker',
    developer: 'Docker Inc.',
    description: 'Platform for developing, shipping, and running applications',
    icon: 'https://www.docker.com/wp-content/uploads/2022/03/Moby-logo.png',
    rating: 4.8,
    category: 'Development'
  },
  {
    id: 'google-chrome',
    name: 'Google Chrome',
    developer: 'Google',
    description: 'Fast and secure web browser',
    icon: 'https://www.google.com/chrome/static/images/chrome-logo.svg',
    rating: 4.5,
    category: 'Internet'
  }
];

const HomePage = () => {
  const navigate = useNavigate();
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [installedSoftware, setInstalledSoftware] = useState([]);
  const [user, setUser] = useState(null);

  // Load user data and installed software on mount
  useEffect(() => {
    const loadInitialData = async () => {
      // Load user data
      const userData = localStorage.getItem('user');
      if (userData) {
        setUser(JSON.parse(userData));
      }

      // Load installed software
      try {
        const response = await installationService.getInstalledSoftware();
        const data = Array.isArray(response) ? response : [];
        setInstalledSoftware(data);
      } catch (error) {
        console.error('Error fetching installed software:', error);
        setInstalledSoftware([]);
      }
    };

    loadInitialData();
  }, []); // Only run on mount

  // Filter software based on category
  const filteredSoftware = softwareList
    .map(software => ({
      ...software,
      isInstalled: installedSoftware.some(installed => installed.id === software.id),
      version: installedSoftware.find(installed => installed.id === software.id)?.version
    }))
    .filter(software => 
      selectedCategory === 'all' || 
      software.category.toLowerCase() === categories.find(c => c.id === selectedCategory)?.name.toLowerCase()
    );

  // Enable auto-logout after inactivity
  const handleLogout = useCallback(() => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    navigate('/login');
  }, [navigate]);

  const { showWarning, remainingTime, onStayLoggedIn } = useAutoLogout(handleLogout);

  const handleCategorySelect = useCallback((categoryId) => {
    setSelectedCategory(categoryId);
  }, []);

  const handleAppClick = useCallback((app) => {
    // Prevent navigation for now as it's causing unwanted refreshes
    // navigate(`/app/${app.id}`, { state: { app } });
  }, []);

  const handleInstall = useCallback(async (software) => {
    try {
      await installationService.installSoftware(software.id);
      // Refresh installed software list
      const response = await installationService.getInstalledSoftware();
      const data = Array.isArray(response) ? response : [];
      setInstalledSoftware(data);
    } catch (error) {
      console.error('Error installing software:', error);
    }
  }, []);

  const handleCheck = useCallback(async (software) => {
    try {
      const updateInfo = await installationService.checkForUpdates(software.id);
      if (updateInfo?.hasUpdate) {
        // Handle update available
        console.log('Update available:', updateInfo.latestVersion);
      }
    } catch (error) {
      console.error('Error checking for updates:', error);
    }
  }, []);

  // Check if user is logged in
  useEffect(() => {
    const token = localStorage.getItem('token');
    const user = localStorage.getItem('user');
    
    if (!token || !user) {
      navigate('/login');
      return;
    }

    try {
      const userData = JSON.parse(user);
      if (!userData.role || userData.role !== 'user') {
        navigate('/login');
      }
    } catch (err) {
      console.error('Error parsing user data:', err);
      navigate('/login');
    }
  }, [navigate]);

  return (
    <Box>
      <Header 
        user={user}
        onLogout={handleLogout}
      />
      <LogoutWarning 
        open={showWarning}
        onStayLoggedIn={onStayLoggedIn}
        onLogout={handleLogout}
        remainingTime={remainingTime}
      />
      <Box sx={{ display: 'flex', p: 3, gap: 3 }}>
        {/* Categories Sidebar */}
        <Paper sx={{ width: 240, flexShrink: 0 }}>
          <List>
            {categories.map((category) => (
              <ListItem
                button
                key={category.id}
                selected={selectedCategory === category.id}
                onClick={() => handleCategorySelect(category.id)}
                sx={{
                  '&.Mui-selected': {
                    backgroundColor: 'primary.light',
                    '&:hover': {
                      backgroundColor: 'primary.light',
                    },
                  },
                }}
              >
                <ListItemIcon sx={{ minWidth: 40 }}>
                  {category.icon}
                </ListItemIcon>
                <ListItemText primary={category.name} />
              </ListItem>
            ))}
          </List>
        </Paper>

        {/* Main Content */}
        <Box sx={{ flex: 1 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
            <Typography variant="h4">
              {categories.find(c => c.id === selectedCategory)?.name}
            </Typography>
          </Box>
          <Grid container spacing={3}>
            {filteredSoftware.map((software) => (
              <Grid item xs={12} sm={6} md={4} key={software.id}>
                <AppCard 
                  {...software} 
                  onCardClick={() => handleAppClick(software)}
                  onInstall={() => handleInstall(software)}
                  onCheck={() => handleCheck(software)}
                />
              </Grid>
            ))}
          </Grid>
        </Box>
      </Box>
    </Box>
  );
};

export default HomePage;
