import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import {
  Container,
  Paper,
  Typography,
  Box,
  CircularProgress,
  useTheme,
  useMediaQuery,
  Avatar,
  Grid,
  Chip,
  Divider,
  IconButton,
  Alert,
  Snackbar,
  Card,
  CardContent,
  Button
} from '@mui/material';
import {
  Person as PersonIcon,
  AdminPanelSettings as AdminIcon,
  Engineering as EngineerIcon,
  Email as EmailIcon,
  Edit as EditIcon,
  ArrowBack as ArrowBackIcon,
  Computer as ComputerIcon,
  Memory as MemoryIcon,
  Storage as StorageIcon,
  Speed as SpeedIcon
} from '@mui/icons-material';
import Header from '../components/Header';
import Sidebar from '../components/Sidebar';
import useAutoLogout from '../hooks/useAutoLogout';
import LogoutWarning from '../components/LogoutWarning';

const getRoleIcon = (role) => {
  switch (role) {
    case 'admin':
      return <AdminIcon />;
    case 'engineer':
      return <EngineerIcon />;
    default:
      return <PersonIcon />;
  }
};

const getRoleColor = (role) => {
  switch (role) {
    case 'admin':
      return '#0088FE';
    case 'engineer':
      return '#00C49F';
    default:
      return '#FFBB28';
  }
};

const StatCard = ({ icon: Icon, title, value, color }) => (
  <Card sx={{ height: '100%' }}>
    <CardContent>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
        <Icon sx={{ color, mr: 1 }} />
        <Typography variant="subtitle2" color="text.secondary">
          {title}
        </Typography>
      </Box>
      <Typography variant="h6" component="div" sx={{ color }}>
        {value}
      </Typography>
    </CardContent>
  </Card>
);

const UserDetailsPage = () => {
  const navigate = useNavigate();
  const { userId } = useParams();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [currentUser, setCurrentUser] = useState(null);
  const [userDetails, setUserDetails] = useState(null);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });

  useEffect(() => {
    const userData = localStorage.getItem('user');
    if (userData) {
      const parsedUser = JSON.parse(userData);
      if (parsedUser.role !== 'admin') {
        navigate('/login');
        return;
      }
      setCurrentUser(parsedUser);
    } else {
      navigate('/login');
    }
  }, [navigate]);

  useEffect(() => {
    const fetchUserDetails = async () => {
      try {
        const token = localStorage.getItem('token');
        const response = await fetch(`http://localhost:3007/users/${userId}`, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        if (!response.ok) {
          throw new Error('Failed to fetch user details');
        }

        const data = await response.json();
        setUserDetails(data);
      } catch (error) {
        console.error('Error fetching user details:', error);
        setError('Failed to load user details');
      } finally {
        setLoading(false);
      }
    };

    if (userId) {
      fetchUserDetails();
    }
  }, [userId]);

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    navigate('/login');
  };

  const { showWarning, remainingTime, onStayLoggedIn } = useAutoLogout(handleLogout);

  const handleSidebarClose = () => {
    setSidebarOpen(false);
  };

  const handleSidebarToggle = () => {
    setSidebarOpen(!sidebarOpen);
  };

  const handleNavigate = (path) => {
    navigate(path);
  };

  const handleEditClick = () => {
    navigate(`/user-edit/${userId}`);
  };

  const handleBack = () => {
    navigate('/admin-dashboard');
  };

  const handleSnackbarClose = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box sx={{ display: 'flex' }}>
      <Header 
        user={currentUser}
        onLogout={handleLogout}
        onSidebarToggle={handleSidebarToggle}
      />
      <Sidebar
        open={sidebarOpen}
        onClose={handleSidebarClose}
        onNavigate={handleNavigate}
      />
      <LogoutWarning 
        open={showWarning}
        onStayLoggedIn={onStayLoggedIn}
        onLogout={handleLogout}
        remainingTime={remainingTime}
      />
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          height: '100vh',
          overflow: 'auto',
          pt: 8,
          px: 2,
          pb: 4,
          ml: { sm: `${isMobile ? 0 : 50}px` },
          backgroundColor: theme.palette.grey[100]
        }}
      >
        <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
          {error ? (
            <Alert severity="error" sx={{ mb: 3 }}>{error}</Alert>
          ) : userDetails && (
            <>
              <Box sx={{ mb: 3, display: 'flex', alignItems: 'center' }}>
                <IconButton onClick={handleBack} sx={{ mr: 2 }}>
                  <ArrowBackIcon />
                </IconButton>
                <Typography variant="h5" component="h2" sx={{ flex: 1, fontWeight: 600 }}>
                  User Details
                </Typography>
                <Button
                  variant="contained"
                  startIcon={<EditIcon />}
                  onClick={handleEditClick}
                >
                  Edit User
                </Button>
              </Box>

              <Grid container spacing={3}>
                {/* User Profile Card */}
                <Grid item xs={12}>
                  <Paper sx={{ p: 3, mb: 3 }}>
                    <Box sx={{ display: 'flex', alignItems: 'flex-start', mb: 3 }}>
                      <Avatar
                        src={userDetails.avatar}
                        alt={userDetails.name}
                        sx={{ 
                          width: 100, 
                          height: 100,
                          mr: 3,
                          border: `3px solid ${getRoleColor(userDetails.role)}`
                        }}
                      />
                      <Box sx={{ flex: 1 }}>
                        <Typography variant="h4" gutterBottom>
                          {userDetails.name}
                        </Typography>
                        <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                          <EmailIcon sx={{ mr: 1, color: 'text.secondary' }} />
                          <Typography variant="body1" color="text.secondary">
                            {userDetails.email}
                          </Typography>
                        </Box>
                        <Chip
                          icon={getRoleIcon(userDetails.role)}
                          label={userDetails.role.charAt(0).toUpperCase() + userDetails.role.slice(1)}
                          sx={{
                            backgroundColor: `${getRoleColor(userDetails.role)}20`,
                            color: getRoleColor(userDetails.role),
                            '& .MuiChip-icon': {
                              color: getRoleColor(userDetails.role)
                            }
                          }}
                        />
                      </Box>
                    </Box>
                  </Paper>
                </Grid>

                {/* System Configuration */}
                {userDetails.systemConfig && (
                  <Grid item xs={12}>
                    <Paper sx={{ p: 3 }}>
                      <Typography variant="h6" gutterBottom sx={{ mb: 3 }}>
                        System Configuration
                      </Typography>
                      <Grid container spacing={3}>
                        <Grid item xs={12} sm={6} md={3}>
                          <StatCard
                            icon={ComputerIcon}
                            title="Operating System"
                            value={`${userDetails.systemConfig.osName} ${userDetails.systemConfig.osVersion}`}
                            color={theme.palette.primary.main}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6} md={3}>
                          <StatCard
                            icon={MemoryIcon}
                            title="Memory"
                            value={`${userDetails.systemConfig.freeMemory}GB / ${userDetails.systemConfig.totalMemory}GB`}
                            color={theme.palette.success.main}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6} md={3}>
                          <StatCard
                            icon={StorageIcon}
                            title="Storage"
                            value={`${userDetails.systemConfig.freeDiskSpace}GB / ${userDetails.systemConfig.totalDiskSpace}GB`}
                            color={theme.palette.warning.main}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6} md={3}>
                          <StatCard
                            icon={SpeedIcon}
                            title="CPU"
                            value={`${userDetails.systemConfig.cpuModel} (${userDetails.systemConfig.cpuCores} cores)`}
                            color={theme.palette.info.main}
                          />
                        </Grid>
                      </Grid>
                    </Paper>
                  </Grid>
                )}

                {/* Installed Software */}
                {userDetails.installedSoftware && userDetails.installedSoftware.length > 0 && (
                  <Grid item xs={12}>
                    <Paper sx={{ p: 3 }}>
                      <Typography variant="h6" gutterBottom sx={{ mb: 3 }}>
                        Installed Software
                      </Typography>
                      <Grid container spacing={2}>
                        {userDetails.installedSoftware.map((software) => (
                          <Grid item xs={12} sm={6} md={4} key={software._id}>
                            <Card>
                              <CardContent>
                                <Typography variant="h6" gutterBottom>
                                  {software.name}
                                </Typography>
                                <Typography color="text.secondary" gutterBottom>
                                  Version: {software.version}
                                </Typography>
                                <Chip
                                  label={software.status}
                                  color={software.status === 'installed' ? 'success' : 'default'}
                                  size="small"
                                />
                              </CardContent>
                            </Card>
                          </Grid>
                        ))}
                      </Grid>
                    </Paper>
                  </Grid>
                )}
              </Grid>
            </>
          )}
        </Container>
      </Box>

      {/* Snackbar for notifications */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleSnackbarClose}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      >
        <Alert 
          onClose={handleSnackbarClose} 
          severity={snackbar.severity}
          variant="filled"
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default UserDetailsPage;
