const API_BASE_URL = 'http://localhost:3007/user-software';

const makeAuthenticatedRequest = async (url, options = {}) => {
  const token = localStorage.getItem('token');
  return fetch(url, {
    ...options,
    headers: {
      ...options.headers,
      'Authorization': `Bearer ${token}`
    }
  });
};

export const installationService = {
  async getInstalledSoftware() {
    try {
      const response = await makeAuthenticatedRequest(`${API_BASE_URL}`);
      if (!response.ok) {
        throw new Error('Failed to fetch installed software');
      }
      const data = await response.json();
      return Array.isArray(data) ? data : [];
    } catch (error) {
      console.error('Error fetching installed software:', error);
      return [];
    }
  },

  async installSoftware(softwareId) {
    try {
      const response = await makeAuthenticatedRequest(`${API_BASE_URL}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          id: softwareId,
          version: '1.0.0'
        })
      });
      if (!response.ok) {
        throw new Error('Failed to install software');
      }
      const data = await response.json();
      return data;
    } catch (error) {
      console.error('Installation error:', error);
      throw error;
    }
  },

  async checkForUpdates(softwareId) {
    try {
      const response = await makeAuthenticatedRequest(`${API_BASE_URL}/${softwareId}/updates`);
      if (!response.ok) {
        throw new Error('Failed to check for updates');
      }
      const data = await response.json();
      return {
        hasUpdate: data.hasUpdate || false,
        currentVersion: data.currentVersion || '1.0.0',
        latestVersion: data.latestVersion || '1.0.0'
      };
    } catch (error) {
      console.error('Error checking for updates:', error);
      return {
        hasUpdate: false,
        currentVersion: null,
        latestVersion: null
      };
    }
  },

  async updateSoftware(softwareId) {
    try {
      const response = await makeAuthenticatedRequest(`${API_BASE_URL}/${softwareId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          version: '1.0.0'
        })
      });
      if (!response.ok) {
        throw new Error('Failed to update software');
      }
      const data = await response.json();
      return data;
    } catch (error) {
      console.error('Update error:', error);
      throw error;
    }
  }
};
